@mixin titulo-1 {
  font-family: 'Gotham';
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 43px;
  text-align: center;
}

@mixin botao-1{
  font-family: 'Gotham';
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
}

.suspended__wrapper{
  width: calc(100% - (10% * 2));
  margin: 3.5rem auto 0;
  padding: 5rem 0;
  border-radius: 1rem;
  background: #FFFFFF;
  box-shadow: 0px 2px 35px -7px rgba(0, 0, 0, 0.1); 
}

.suspended__title{
  color: var(--main-color);
}

.suspended-nav{
  width: calc(100% - (15% * 2));
  margin: 5.25rem auto 0;
}

.suspended__menu{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  .suspended__menu-link{

  }
}

.btn{
  border-radius: 1rem;
  padding: 1rem 0;
  text-align: center;
  @include botao-1;
  width: 350px;
  display: block;
  transition: .2s ease-in-out;
  margin: 0 auto;
  &:hover{
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    
  }
} 

.btn__yellow{
  background-color: var(--yellow-tonalite);
  color: var(--main-color);
  &.btn__yellow--position{
    margin-top: 5.75rem;
  }
}
